<template>
  <div class="banner">
    <div class="bg"
      :style="{'background-image' : 'url(' + img + ')'}"></div>
  </div>
</template>
<script>
export default {
  props: ['img']
};
</script>
<style lang="stylus" >
.banner {
  display: block;
  position: relative;
  height: 300px;
  overflow: hidden;
}
</style>



